import React from "react";
import { Redirect } from "react-router-dom";
import auth from "../../services/authService";
import Form from "../common/form";
import "react-quill/dist/quill.snow.css";
import "quill/dist/quill.snow.css";
class ViewTutorial extends Form {
  state = {
    author_id: "",
    validTutorial: "",
    data: {
      title: "",
      file: "",
      content: "",
      release_date: "",
      user: { firstName: "", lastName: "", userPhoto: "" },
    },
  };
  async componentDidMount() {
    try {
      const result = await auth.getTutorial(this.props.match.params.id);
      const resData = result.data.data;
      const validTutorial = true;
      const data = { ...this.state.data };
      console.log(data);
      data.title = resData.title;
      data.file = resData.tfile;
      data.content = resData.content;
      data.release_date = resData.release_date;
      const author_id = resData.writer_id;
      const userProfile = await auth.getUserProfile(author_id);
      data.user = userProfile.data[0];
      this.setState({ validTutorial, data, author_id });
    } catch (e) {
      const validTutorial = false;
      this.setState({ validTutorial });
      //toast.error(e.response.data.error[0]);
    }
  }

  render() {
    const { file, user, release_date, content, title } = this.state.data;
    const { validTutorial } = this.state;
    console.log(file);
    return (
      <React.Fragment>
        {validTutorial === "" && <div id="loader"></div>}
        {validTutorial === false && <Redirect to="/tutorials" />}
        {validTutorial === true && (
          <div className="card col-12 px-0">
            <div className="card-body px-0">
              <div className="d-flex align-self-center  justify-content-center">
                <img
                  src={auth.apiUrl + user.userPhoto}
                  alt="img"
                  className="view-authorPic d-flex "
                />
              </div>
              <div className="d-flex align-items-center justify-content-center px-5 mx-xs-5">
                <p className="py-2 border-bottom border-1 border-gray">
                  <small className="text-muted"> ارسال شده توسط </small>
                  <span className="font-weight-bold px-2">
                    {user.first_name + " " + user.last_name}
                  </span>
                </p>
              </div>
              <h6 className="text-center font-weight-bold pt-5 pb-3">
                {title}
              </h6>
              <div
                className="p-sm-3 p-md-5 mx-sm-3 mx-md-5 news-content ql-editor"
                dangerouslySetInnerHTML={{
                  __html: content,
                }}
              ></div>
            </div>
            <p className="px-sm-3 px-md-5 py-1  mx-sm-3 mx-md-5">
              <a href={auth.apiUrl + file}>
                <i className="fas fa-download text-success"></i> دانلود فایل
                ضمیمه
              </a>
            </p>
            <div className="d-flex align-items-center justify-content-center px-5 mx-xs-5">
              <p className="px-2">
                <small> در تاریخ </small>
                <span className="font-weight-bold px-2">{release_date}</span>
              </p>
            </div>
          </div>
        )}
      </React.Fragment>
    );
  }
}

export default ViewTutorial;
