import React from "react";
import { toast } from "react-toastify";
import Joi from "joi-browser";
import Form from "../common/form";
import auth from "../../services/authService";

class ChangePassFromProfile extends Form {
  state = {
    data: { oldPassword: "", newPassword: "", confirmNewPassword: "" },
    errors: {},
  };
  schema = {
    oldPassword: Joi.string() //change to number
      .required()
      .label("رمز عبور قدیمی")
      .error((errors) => this.ErrorsLang(errors)),
    newPassword: Joi.string()
      .required()
      .label("رمز عبور جدید")
      .error((errors) => this.ErrorsLang(errors)),
    confirmNewPassword: Joi.any()
      .valid(Joi.ref("newPassword"))
      .label("تکرار رمز عبور جدید")
      .error((errors) => this.ErrorsLang(errors)),
  };

  doSubmit = async () => {
    try {
      const { oldPassword, newPassword } = this.state.data;
      await auth.changePassFromProfile(oldPassword, newPassword);
      await auth.logout();
      window.location = process.env.PUBLIC_URL + "/";
    } catch (ex) {
      console.log(ex);
      toast.error("رمز عبور شما تغییر نکرد :(");
    }
  };
  render() {
    return (
      <div className="form-login d-flex align-items-center mt-4 pt-5">
        <div className="card col-lg-6 col-md-5 col-sm-7 col-12 mx-auto mt-5 px-5 pb-5">
          <span className="text-center login-icon mt-n5">
            <i className="fas fa-cog bg-white spin"></i>
          </span>
          <h4 className="text-center text-muted pt-3">تغییر رمز عبور</h4>
          <form onSubmit={this.handleSubmit}>
            {this.renderInput(
              "oldPassword",
              "رمز عبور قدیمی",
              "",
              "",
              "password"
            )}
            {this.renderInput(
              "newPassword",
              "رمز عبور جدید",
              "",
              "",
              "password"
            )}
            {this.renderInput(
              "confirmNewPassword",
              "تکرار رمز عبور جدید",
              "",
              "",
              "password"
            )}
            <div className="text-center pt-4">
              {this.renderButton("اعمال تغییرات", "mt-3")}
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default ChangePassFromProfile;
