import React, { Component } from "react";
import { toast } from "react-toastify";
import _ from "lodash";
import $ from "jquery";
import Modal from "../common/modal";
import Lbs from "./../common/lbs";
import TutorialsTable from "./tutorialsTable";
import Pagination from "../common/pagination";
import auth from "../../services/authService";
import { paginate } from "../../js/paginate";

class Tutorials extends Component {
  state = {
    tutorials: [],
    IsRequestDone: false,
    modalData: "",
    currentPage: 1,
    pageSize: 4,
    sortColumn: { path: "title", order: "asc" },
  };

  async componentDidMount() {
    $("#delete_tutorial").on("show.bs.modal");
    try {
      const result = await auth.getAllTutorials();
      const tutorials = result.data.data;
      this.setState({ tutorials, IsRequestDone: true });
    } catch (error) {
      toast.error("مشکلی در ارتباط با سرور پیش آمد :(");
      this.setState({ IsRequestDone: true });
    }
  }

  handleDelete = async (tutorialId) => {
    const originalTutorial = this.state.tutorials;
    const tutorials = this.state.tutorials.filter((n) => n.id !== tutorialId);
    this.setState({ tutorials });
    try {
      await auth.deleteTutorial(tutorialId);
      toast.success("آموزش با موفقیت حذف شد.");
    } catch (e) {
      toast.error("حذف آموزش با شکست مواجه شد..");
      this.setState({ tutorials: originalTutorial });
    }
  };

  handleModal = (modalData) => {
    this.setState({ modalData });
    $("#delete_tutorial").modal("show");
  };
  handleEdit = (tutorial) => {
    window.location = process.env.PUBLIC_URL + "/edit-tutorial/" + tutorial.id;
  };
  handleView = (tutorial) => {
    window.location = process.env.PUBLIC_URL + "/tutorials/" + tutorial.id;
  };
  handlePageChange = (page) => {
    this.setState({ currentPage: page });
  };

  handleSort = (sortColumn) => {
    this.setState({ sortColumn });
  };

  getPagedData = () => {
    const {
      pageSize,
      currentPage,
      sortColumn,
      tutorials: allTutorials,
    } = this.state;

    const sorted = _.orderBy(
      allTutorials,
      [sortColumn.path],
      [sortColumn.order]
    );

    const tutorials = paginate(sorted, currentPage, pageSize);

    return { totalCount: allTutorials.length, data: tutorials };
  };
  render() {
    const { length: count } = this.state.tutorials;
    const { pageSize, currentPage, sortColumn, IsRequestDone } = this.state;
    if (!IsRequestDone)
      return (
        <div className="card col-12">
          <div className="card-body">
            <Lbs />
          </div>
        </div>
      );
    if (count === 0)
      return (
        <div className="card col-12">
          <div className="card-body">
            <p>چیزی پیدا نشد :(</p>
          </div>
        </div>
      );

    const { totalCount, data: tutorials } = this.getPagedData();

    return (
      <React.Fragment>
        <Modal
          modalId="delete_tutorial"
          dataId={this.state.modalData}
          handleDelete={this.handleDelete}
        />
        <div className="card col-12">
          <div className="card-body">
            <div className="col">
              <p className="small text-muted">نتیجه: {totalCount} آموزش</p>
              <TutorialsTable
                tutorials={tutorials}
                sortColumn={sortColumn}
                onEdit={this.handleEdit}
                onClick={this.handleModal}
                onView={this.handleView}
                onSort={this.handleSort}
              />
              <Pagination
                itemsCount={totalCount}
                pageSize={pageSize}
                currentPage={currentPage}
                onPageChange={this.handlePageChange}
              />
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Tutorials;
