import React from "react";
import Joi from "joi-browser";
import { toast } from "react-toastify";
import Form from "../common/form";
import auth from "../../services/authService";
import { Redirect, Link } from "react-router-dom";

class EditClass extends Form {
  state = {
    validClass: "",
    data: { name: "", section_id: "0", id: "" },
    section_choices: [
      { name: "پیش دبستانی", _id: "0" },
      { name: "اول", _id: "1" },
      { name: "دوم", _id: "2" },
      { name: "سوم", _id: "3" },
      { name: "چهارم", _id: "4" },
      { name: "پنجم", _id: "5" },
      { name: "ششم", _id: "6" },
      { name: "هفتم", _id: "7" },
      { name: "هشتم", _id: "8" },
      { name: "نهم", _id: "9" },
    ],
    errors: {},
  };
  schema = {
    name: Joi.string()
      .required()
      .label("نام کلاس")
      .error((errors) => this.ErrorsLang(errors)),
    section_id: Joi.string()
      .required()
      .label("مقطع")
      .error((errors) => this.ErrorsLang(errors)),
    id: Joi.any(),
  };
  async componentDidMount() {
    if (this.props.match.params.id === "0") return;
    try {
      const result = await auth.getClass(this.props.match.params.id);
      const resData = result.data.data;
      const validClass = true;
      let data = { ...this.state.data };
      data.name = resData.name;
      data.section_id = resData.section_id;
      data.id = resData.id;
      this.setState({ validClass, data });
    } catch (e) {
      const validClass = false;
      this.setState({ validClass });
      toast.error(e.response.data.error[0]);
    }
  }
  doSubmit = async () => {
    try {
      const { data } = this.state;
      await auth.updateClass(data);
      //   console.log(res);
      window.location = process.env.PUBLIC_URL + "/classes/";
    } catch (ex) {
      //   console.log(ex.response);
      toast.error("تغییرات اعمال نشد :(");
      if (ex.response && ex.response.status === 401) {
        const errors = { ...this.state.errors };
        errors.username = ex.response.data.detail; //translate it
        this.setState({ errors });
      }
    }
  };
  render() {
    if (this.props.match.params.id === "0") return <Redirect to="/classes" />;
    const { validClass } = this.state;
    return (
      <React.Fragment>
        {validClass === "" && <div id="loader"></div>}
        {validClass === false && <Redirect to="/classes" />}
        {validClass === true && (
          <div className="form-login d-flex align-items-center mt-4 pt-5">
            <div className="card col-lg-4 col-md-5 col-sm-7 col-10 mx-auto mt-5 px-5 pb-5">
              <span className="text-center login-icon mt-n5">
                <i className="fas fa-door-closed bg-white"></i>
              </span>
              <h4 className="text-center text-muted pt-3">تغییر کلاس</h4>
              <form onSubmit={this.handleSubmit}>
                {this.renderInput("name", "نام کلاس")}
                {this.renderSelect(
                  "section_id",
                  "مقطع",
                  this.state.section_choices,
                  " "
                )}
                <div className="text-center pt-4">
                  {this.renderButton("ثبت", "mt-3")}
                </div>
                <span className="d-flex justify-content-center pt-4">
                  <Link to="/classes" className="text-muted">
                    بازگشت به لیست کلاس ها
                  </Link>
                </span>
              </form>
            </div>
          </div>
        )}
      </React.Fragment>
    );
  }
}

export default EditClass;
