import React, { Component } from "react";
import Table from "../common/table/table";
class TutorialsTable extends Component {
  columns = [
    { path: "title", label: "تیتر" },
    { path: "writer.first_name", label: "نام" },
    { path: "release_date", label: "تاریخ انتشار" },
    {
      label: "مدیریت",
      key: "manage",
      content: (
        tutorials // fix it
      ) => (
        <React.Fragment>
          <button
            onClick={() => this.props.onView(tutorials)}
            className="btn btn-outline-primary btn-sm"
            style={{ height: "35px", width: "35px" }}
          >
            <i className="fas fa-eye d-flex align-self-center justify-content-center py-1"></i>
          </button>
          <button
            onClick={() => this.props.onEdit(tutorials)}
            className="btn btn-outline-info btn-sm mx-2"
            style={{ height: "35px", width: "35px" }}
          >
            <i className="fas fa-edit d-flex align-self-center justify-content-center py-1"></i>
          </button>
          <button
            onClick={() => this.props.onClick(tutorials.id)}
            className="btn btn-outline-danger btn-sm"
            style={{ height: "35px", width: "35px" }}
          >
            <i className="fas fa-trash-alt d-flex align-self-center justify-content-center py-1"></i>
          </button>
        </React.Fragment>
      ),
    },
  ];
  shortendTitle(tutorials) {
    for (let i = 0; i < tutorials.length; i++) {
      if (tutorials[i].title.length > 50) {
        tutorials[i].title = tutorials[i].title.slice(0, 47) + "...";
      }
    }
    return tutorials;
  }
  render() {
    const { onSort, sortColumn } = this.props;
    let { tutorials } = this.props;
    tutorials = this.shortendTitle(tutorials);
    return (
      <Table
        columns={this.columns}
        data={tutorials}
        sortColumn={sortColumn}
        onSort={onSort}
      />
    );
  }
}

export default TutorialsTable;
