import React, { Component } from "react";
import Table from "../common/table/table";

class ClassesTable extends Component {
  columns = [
    { path: "name", label: "نام کلاس" },
    { path: "section.name", label: "مقطع" },
    {
      label: "برنامه هفتگی",
      key: "schedule",
      content: (
        classRoom // fix it
      ) => (
        <div className="text-center">
          <button
            onClick={() => this.props.onScheduleAdd(classRoom)}
            className="btn btn-outline-info btn-sm"
            style={{ height: "35px", width: "35px" }}
          >
            <i className="far fa-calendar-plus d-flex align-self-center justify-content-center py-1"></i>
          </button>
          <button
            onClick={() => this.props.onscheduleView(classRoom)}
            className="btn btn-outline-success btn-sm mr-2"
            style={{ height: "35px", width: "35px" }}
          >
            <i className="far fa-calendar-alt d-flex align-self-center justify-content-center py-1"></i>
          </button>
        </div>
      ),
    },
    {
      label: "مدیریت",
      key: "manage",
      content: (
        classRoom // fix it
      ) => (
        <React.Fragment>
          <button
            onClick={() => this.props.onEdit(classRoom)}
            className="btn btn-outline-info btn-sm mx-2"
            style={{ height: "35px", width: "35px" }}
          >
            <i className="fas fa-edit d-flex align-self-center justify-content-center py-1"></i>
          </button>
          <button
            onClick={() => this.props.onClick(classRoom.id)}
            className="btn btn-outline-danger btn-sm"
            style={{ height: "35px", width: "35px" }}
          >
            <i className="fas fa-trash-alt d-flex align-self-center justify-content-center py-1"></i>
          </button>
        </React.Fragment>
      ),
    },
  ];
  render() {
    const { classes, onSort, sortColumn } = this.props;

    return (
      <Table
        columns={this.columns}
        data={classes}
        sortColumn={sortColumn}
        onSort={onSort}
      />
    );
  }
}

export default ClassesTable;
