import React, { Component } from "react";
import Table from "../common/table/table";
import auth from "../../services/authService";
class NewsTable extends Component {
  columns = [
    {
      label: "",
      key: "newsPic",
      content: (
        news // fix it
      ) => (
        <img
          src={auth.apiUrl + news.pic}
          alt="newspic"
          className="table-newsPic"
        />
      ),
    },
    { path: "title", label: "تیتر" },
    { path: "author.first_name", label: "نام" },
    {
      label: "",
      key: "authorPic",
      content: (
        news // fix it
      ) => (
        <img
          src={auth.apiUrl + news.author.userPhoto}
          alt="authorPic"
          className="table-authorPic"
        />
      ),
    },
    { path: "release_date", label: "تاریخ انتشار" },
    {
      label: "مدیریت",
      key: "manage",
      content: (
        news // fix it
      ) => (
        <React.Fragment>
          <button
            onClick={() => this.props.onView(news)}
            className="btn btn-outline-primary btn-sm"
            style={{ height: "35px", width: "35px" }}
          >
            <i className="fas fa-eye d-flex align-self-center justify-content-center py-1"></i>
          </button>
          <button
            onClick={() => this.props.onEdit(news)}
            className="btn btn-outline-info btn-sm mx-2"
            style={{ height: "35px", width: "35px" }}
          >
            <i className="fas fa-edit d-flex align-self-center justify-content-center py-1"></i>
          </button>
          <button
            onClick={() => this.props.onClick(news.id)}
            className="btn btn-outline-danger btn-sm"
            style={{ height: "35px", width: "35px" }}
          >
            <i className="fas fa-trash-alt d-flex align-self-center justify-content-center py-1"></i>
          </button>
        </React.Fragment>
      ),
    },
  ];
  shortendTitle(news) {
    for (let i = 0; i < news.length; i++) {
      if (news[i].title.length > 50) {
        news[i].title = news[i].title.slice(0, 47) + "...";
      }
    }
    return news;
  }
  render() {
    const { onSort, sortColumn } = this.props;
    let { news } = this.props;
    news = this.shortendTitle(news);
    return (
      <Table
        columns={this.columns}
        data={news}
        sortColumn={sortColumn}
        onSort={onSort}
      />
    );
  }
}

export default NewsTable;
