export function logout() {
  localStorage.removeItem(accessToken);
  localStorage.removeItem(refreshToken);
}
export async function changePassFromProfile(old_password, new_password) {
  const apiEndPoint = apiUrl + "/Users/App/user/ChangePassInProfile/";
  await http.post(apiEndPoint, { old_password, new_password });
}
export async function changePassByModerator(user_id, new_password) {
  const apiEndPoint =
    apiUrl + "/Users/App/user/ModeratorChangePass/?user_id=" + user_id;
  await http.post(apiEndPoint, { new_password });
}
export async function sendPassResetCode(nationalCode) {
  let instance = http.createInstance();
  delete instance.defaults.headers.common["Authorization"];

  const apiEndPoint =
    apiUrl + "/Users/App/user/sendResetCode/?national_code=" + nationalCode;

  return await instance.post(apiEndPoint);
}
export async function passReset(nationalCode, resetCode) {
  let instance = http.createInstance();
  delete instance.defaults.headers.common["Authorization"];

  const apiEndPoint =
    apiUrl +
    `/Users/App/user/changePass/?reset_code=${resetCode}&national_code=${nationalCode}`;

  return await instance.post(apiEndPoint);
}
export function getCurrentUser() {
  try {
    const token = localStorage.getItem(accessToken);
    const { user_id: userId } = jwtDecode(token);

    return userId;
  } catch (ex) {
    return null;
  }
}
export async function getUserProfile(userId) {
  const apiEndPoint = `${apiUrl}/Users/App/user/profile/?user_id=${userId}`;
  try {
    const userProfile = await http.get(apiEndPoint);
    return userProfile.data;
  } catch (ex) {
    logout(); //fix this error too :D
    //http.toast.error("شما نیاز به لاگین کردن دارید"); temperory fixed!
    //console.log(ex.response);
    return null;
  }
}
export async function getUserDoc(userId) {
  const apiEndPoint = `${apiUrl}/Users/App/userDoc/api/?user_id=${userId}`;
  try {
    const userDoc = await http.get(apiEndPoint);
    return userDoc.data;
  } catch (ex) {
    //http.toast.error("شما نیاز به لاگین کردن دارید");
    //console.log(ex.response);
    return null;
  }
}
