export async function uploadImage(file) {
  //fix this for errors
  const userId = getCurrentUser();
  const uploadData = new FormData();
  uploadData.append("user_id", userId);
  uploadData.append("image", file, file.name);
  const img = await http.post(apiUrl + "/Users/App/images/api/", uploadData);
  return img;
}

export async function addNews(data) {
  const { title, img, content } = data;
  const news = {
    title: title,
    content: content,
    pic: img,
    author_id: getCurrentUser(),
    release_date: moment().locale("fa").format("DD MMMM YYYY"),
  };
  const apiEndPoint = apiUrl + "/News/App/api/";
  await http.post(apiEndPoint, news);
}
export async function updateNews(data) {
  const { title, img, content, author_id, release_date, id } = data;
  const news = {
    title: title,
    content: content,
    pic: img,
    author_id: author_id,
    release_date: release_date,
  };
  const apiEndPoint = apiUrl + "/News/App/api/?id=" + id;
  await http.put(apiEndPoint, news);
}
