import React, { Component } from "react";
import { toast } from "react-toastify";
import _ from "lodash";
import $ from "jquery";
import Modal from "../common/modal";
import Lbs from "./../common/lbs";
import NewsTable from "./newsTable";
import Pagination from "../common/pagination";
import auth from "../../services/authService";
import { paginate } from "../../js/paginate";

class News extends Component {
  state = {
    news: [],
    IsRequestDone: false,
    modalData: "",
    currentPage: 1,
    pageSize: 4,
    sortColumn: { path: "title", order: "asc" },
  };

  async componentDidMount() {
    $("#delete_news").on("show.bs.modal", function (e) {
      var bookId = $(e.relatedTarget).data("book-id");
      $(e.currentTarget).find('input[name="bookId"]').val(bookId);
    });
    try {
      const result = await auth.getAllNews();
      const news = result.data.data;
      this.setState({ news, IsRequestDone: true });
    } catch (error) {
      toast.error("مشکلی در ارتباط با سرور پیش آمد :(");
      this.setState({ IsRequestDone: true });
    }
  }

  handleDelete = async (newsId) => {
    const originalNews = this.state.news;
    const news = this.state.news.filter((n) => n.id !== newsId);
    this.setState({ news });
    try {
      await auth.deleteNews(newsId);
      toast.success("خبر با موفقیت حذف شد.");
    } catch (e) {
      toast.error("حذف خبر با شکست مواجه شد..");
      this.setState({ news: originalNews });
    }
  };

  handleModal = (modalData) => {
    // console.log("before setstate");
    this.setState({ modalData });
    // console.log("after setstate");
    $("#delete_news").modal("show");
  };
  handleEdit = (singleNews) => {
    window.location = process.env.PUBLIC_URL + "/edit-news/" + singleNews.id;
  };
  handleView = (singleNews) => {
    window.location = process.env.PUBLIC_URL + "/news/" + singleNews.id;
  };
  handlePageChange = (page) => {
    this.setState({ currentPage: page });
  };

  handleSort = (sortColumn) => {
    this.setState({ sortColumn });
  };

  getPagedData = () => {
    const { pageSize, currentPage, sortColumn, news: allnews } = this.state;

    const sorted = _.orderBy(allnews, [sortColumn.path], [sortColumn.order]);

    const news = paginate(sorted, currentPage, pageSize);

    return { totalCount: allnews.length, data: news };
  };
  render() {
    // console.log("im render");
    const { length: count } = this.state.news;
    const { pageSize, currentPage, sortColumn, IsRequestDone } = this.state;
    if (!IsRequestDone)
      return (
        <div className="card col-12">
          <div className="card-body">
            <Lbs />
          </div>
        </div>
      );
    if (count === 0)
      return (
        <div className="card col-12">
          <div className="card-body">
            <p>چیزی پیدا نشد :(</p>
          </div>
        </div>
      );

    const { totalCount, data: news } = this.getPagedData();

    return (
      <React.Fragment>
        <Modal
          modalId="delete_news"
          dataId={this.state.modalData}
          handleDelete={this.handleDelete}
        />
        <div className="card col-12">
          <div className="card-body">
            <div className="col">
              <p className="small text-muted">نتیجه: {totalCount} خبر</p>
              <NewsTable
                news={news}
                sortColumn={sortColumn}
                onEdit={this.handleEdit}
                onClick={this.handleModal}
                onView={this.handleView}
                onSort={this.handleSort}
              />
              <Pagination
                itemsCount={totalCount}
                pageSize={pageSize}
                currentPage={currentPage}
                onPageChange={this.handlePageChange}
              />
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default News;
