import React from "react";
import Joi from "joi-browser";
import { toast } from "react-toastify";
import Form from "../common/form";
import auth from "../../services/authService";
import { Link } from "react-router-dom";

class AddClass extends Form {
  state = {
    data: { name: "", section_id: "0" },
    section_choices: [
      { name: "پیش دبستانی", _id: "0" },
      { name: "اول", _id: "1" },
      { name: "دوم", _id: "2" },
      { name: "سوم", _id: "3" },
      { name: "چهارم", _id: "4" },
      { name: "پنجم", _id: "5" },
      { name: "ششم", _id: "6" },
      { name: "هفتم", _id: "7" },
      { name: "هشتم", _id: "8" },
      { name: "نهم", _id: "9" },
    ],
    errors: {},
  };
  schema = {
    name: Joi.string()
      .required()
      .label("نام کلاس")
      .error((errors) => this.ErrorsLang(errors)),
    section_id: Joi.string()
      .required()
      .label("مقطع")
      .error((errors) => this.ErrorsLang(errors)),
  };
  doSubmit = async () => {
    try {
      const { data } = this.state;
      await auth.addClass(data);
      // console.log(res);
      //window.location = process.env.PUBLIC_URL + "/classes/";
      this.setState({ data: { name: "", section_id: "0" } });
    } catch (ex) {
      // console.log(ex.response);
      toast.error("کلاس ثبت نشد :(");
    }
  };
  render() {
    return (
      <div className="form-login d-flex align-items-center">
        <div className="card col-lg-4 col-md-5 col-sm-7 col-10 mx-auto mt-5 px-5 pb-5">
          <span className="text-center login-icon mt-n5">
            <i className="fas fa-door-closed bg-white"></i>
          </span>
          <h4 className="text-center text-muted pt-3">ثبت کلاس جدید</h4>
          <form onSubmit={this.handleSubmit}>
            {this.renderInput("name", "نام کلاس")}
            {this.renderSelect(
              "section_id",
              "مقطع",
              this.state.section_choices,
              " "
            )}
            <div className="text-center pt-4">
              {this.renderButton("ثبت", "mt-3")}
            </div>
            <span className="d-flex justify-content-center pt-4">
              <Link to="/classes" className="text-muted">
                بازگشت به لیست کلاس ها
              </Link>
            </span>
          </form>
        </div>
      </div>
    );
  }
}

export default AddClass;
