import React from "react";
import { Redirect } from "react-router-dom";
import auth from "../../services/authService";
import Form from "../common/form";
import "react-quill/dist/quill.snow.css";
import logo from "../../images/no-img.jpg";
import "quill/dist/quill.snow.css";
class ViewNews extends Form {
  state = {
    author_id: "",
    validNews: "",
    data: {
      title: "",
      img: "",
      content: "",
      release_date: "",
      user: { firstName: "", lastName: "", userPhoto: "" },
    },
  };
  async componentDidMount() {
    try {
      const result = await auth.getNews(this.props.match.params.id);
      const resData = result.data.data;
      const validNews = true;
      const data = { ...this.state.data };
      console.log(data);
      data.title = resData.title;
      data.img = resData.pic;
      data.content = resData.content;
      data.release_date = resData.release_date;
      const author_id = resData.author_id;
      const userProfile = await auth.getUserProfile(author_id);
      data.user = userProfile.data[0];
      console.log(data.user);
      this.setState({ validNews, data, author_id });
    } catch (e) {
      const validNews = false;
      this.setState({ validNews });
      //toast.error(e.response.data.error[0]);
    }
  }

  render() {
    const { img, user, release_date, content, title } = this.state.data;
    const { validNews } = this.state;
    return (
      <React.Fragment>
        {validNews === "" && <div id="loader"></div>}
        {validNews === false && <Redirect to="/news" />}
        {validNews === true && (
          <div className="card col-12 px-0">
            <div className="card-body px-0">
              <div className="d-flex align-items-center justify-content-center">
                <div className=" col-md-7 p-0">
                  <img
                    src={img ? auth.apiUrl + img : logo}
                    alt="img"
                    className="rounded img-fluid"
                  />
                </div>
              </div>
              <div className="d-flex align-self-center  justify-content-center mt-n5">
                <img
                  src={auth.apiUrl + user.userPhoto}
                  alt="img"
                  className="view-authorPic d-flex "
                />
              </div>
              <div className="d-flex align-items-center justify-content-center px-5 mx-xs-5">
                <p className="py-2 border-bottom border-1 border-gray">
                  <small className="text-muted"> ارسال شده توسط </small>
                  <span className="font-weight-bold px-2">
                    {user.first_name + " " + user.last_name}
                  </span>
                </p>
              </div>
              <h6 className="text-center font-weight-bold pt-5 pb-3">
                {title}
              </h6>
              <div
                className="p-sm-3 p-md-5 mx-sm-3 mx-md-5 nwes-content ql-editor"
                dangerouslySetInnerHTML={{
                  __html: content,
                }}
              ></div>
            </div>
            <div className="d-flex align-items-center justify-content-center px-5 mx-xs-5">
              <p className="px-2">
                <small> در تاریخ </small>
                <span className="font-weight-bold px-2">{release_date}</span>
              </p>
            </div>
          </div>
        )}
      </React.Fragment>
    );
  }
}

export default ViewNews;
