import React from "react";
import SidebarSingleItem from "./sidebarSingleItem.jsx";
import SidebarMultiItem from "./sidebarMultiItem.jsx";

const SideBar = () => {
  return (
    <div
      className="col-9 col-sm-5 col-md-3 col-lg-2 bg-white p-0 collapse show d-lg-block sticky-top"
      id="sidebar"
    >
      <small className="mr-2 text-muted">منو اصلی</small>
      <ul className="nav flex-column p-0 mt-2 mb-3">
        <SidebarSingleItem
          exact={true}
          to="/"
          label="داشبورد"
          icon="fas fa-desktop pl-1"
        />
        <SidebarMultiItem
          label="مدیریت کاربران"
          icon="fas fa-users-cog pl-1"
          targetId="userManagement"
          items={[
            { label: "ثبت کاربر جدید", to: "/register" },
            { label: "جستجوی کاربر", to: "/users" },
            { label: "ویرایش کاربر", to: "/edit-user/0" },
            { label: "حذف کاربر", to: "/users", navlink: true },
          ]}
        />
        <SidebarMultiItem
          label="مدیریت اخبار"
          icon="fas fa-rss pl-1"
          targetId="newsManagement"
          items={[
            { label: "ثبت خبر جدید", to: "/add-news" },
            { label: "جستجوی اخبار", to: "/news" },
            { label: "ویرایش خبر", to: "/edit-news/0" },
            { label: "حذف خبر", to: "/news", navlink: true },
          ]}
        />
        <SidebarMultiItem
          label="مدیریت آموزش ها"
          icon="far fa-question-circle pl-1"
          targetId="tutorialManagement"
          items={[
            { label: "ثبت آموزش جدید", to: "/add-tutorial" },
            { label: "جستجوی آموزش ها", to: "/tutorials" },
            { label: "ویرایش آموزش", to: "/edit-tutorial/0" },
            { label: "حذف آموزش", to: "/tutorials", navlink: true },
          ]}
        />
        <SidebarMultiItem
          label="مدیریت کلاس ها"
          icon="fas fa-door-closed pl-1"
          targetId="classManagement"
          items={[
            { label: "ثبت کلاس جدید", to: "/add-class" },
            { label: "جستجوی کلاس ها", to: "/classes" },
            { label: "ویرایش کلاس", to: "/edit-class/0" },
            { label: "حذف کلاس", to: "/classes", navlink: true },
          ]}
        />
        <SidebarMultiItem
          label="مدیریت درس ها"
          icon="fas fa-book pl-1"
          targetId="lessonManagement"
          items={[
            { label: "ثبت درس جدید", to: "/add-lesson" },
            { label: "جستجوی درس ها", to: "/lessons" },
            { label: "ویرایش درس", to: "/edit-lesson/0" },
            { label: "حذف درس", to: "/lessons", navlink: true },
          ]}
        />
      </ul>
    </div>
  );
};

export default SideBar;
