import React from "react";
import { toast } from "react-toastify";
import Joi from "joi-browser";
import Form from "../common/form";
import auth from "../../services/authService";
import { Redirect } from "react-router-dom";

class ChangePassByModerator extends Form {
  state = {
    isValidUser: "",
    profilePic: "",
    data: { newPassword: "", confirmNewPassword: "" },
    errors: {},
  };
  schema = {
    newPassword: Joi.string()
      .required()
      .label("رمز عبور جدید")
      .error((errors) => this.ErrorsLang(errors)),
    confirmNewPassword: Joi.any()
      .valid(Joi.ref("newPassword"))
      .label("تکرار رمز عبور جدید")
      .error((errors) => this.ErrorsLang(errors)),
  };
  async componentDidMount() {
    try {
      const { id } = this.props.match.params;
      const res = await auth.getUserProfile(id);
      const isValidUser = true;
      const profilePic = res.data[0].userPhoto;
      this.setState({ profilePic, isValidUser });
      console.log(profilePic);
    } catch (error) {
      console.log(error);
      const isValidUser = false;
      this.setState({ isValidUser });
    }
  }
  doSubmit = async () => {
    const { id } = this.props.match.params;
    try {
      const { newPassword } = this.state.data;
      await auth.changePassByModerator(id, newPassword);
      window.location = process.env.PUBLIC_URL + "/users/" + id;
    } catch (ex) {
      toast.error("رمز عبور تغییر نکرد :(");
    }
  };
  render() {
    const { isValidUser, profilePic } = this.state;
    return (
      <React.Fragment>
        {isValidUser === "" && <div id="loader"></div>}
        {isValidUser === false && <Redirect to="/users" />}
        {isValidUser === true && (
          <div className="form-login d-flex align-items-center mt-4 pt-5">
            <div className="card col-lg-5 col-md-5 col-sm-7 col-12 mx-auto mt-5 px-5 pb-5">
              <span className="text-center">
                <img
                  className="login-icon mt-n5"
                  src={auth.apiUrl + profilePic}
                  alt="profPic"
                  height="150px"
                />
              </span>
              <h4 className="text-center text-muted pt-3">تغییر رمز عبور</h4>
              <form onSubmit={this.handleSubmit}>
                {this.renderInput(
                  "newPassword",
                  "رمز عبور جدید",
                  "",
                  "",
                  "password"
                )}
                {this.renderInput(
                  "confirmNewPassword",
                  "تکرار رمز عبور جدید",
                  "",
                  "",
                  "password"
                )}
                <div className="text-center pt-4">
                  {this.renderButton("اعمال تغییرات", "mt-3")}
                </div>
              </form>
            </div>
          </div>
        )}
      </React.Fragment>
    );
  }
}

export default ChangePassByModerator;
