import React, { Component } from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import { ToastContainer } from "react-toastify";
import LoginForm from "./components/login/loginForm";
import LogOut from "./components/logout/logout";
import RegisterForm from "./components/user_management/registerForm";

import Users from "./components/user_management/users";
import EditUser from "./components/user_management/editUser";
import ChangePassFromProfile from "./components/user_management/changePassFromProfile";

import ChangePassByModerator from "./components/user_management/changePassByModerator";

import ForgotPass from "./components/forgot_pass/forgotPass";

import AddNews from "./components/news_management/addNews";
import EditNews from "./components/news_management/editNews";
import News from "./components/news_management/news";
import ViewNews from "./components/news_management/viewNews";

import AddTutorial from "./components/tutorial_management/addTutorial";
import EditTutorial from "./components/tutorial_management/editTutorial";
import Tutorials from "./components/tutorial_management/tutorials";
import ViewTutorial from "./components/tutorial_management/viewTutorial";

import Home from "./components/home";
import NotFound from "./components/notFound";
import NavBar from "./components/navbar/navbar";
import SideBar from "./components/sidebar/sidebar";
import auth from "./services/authService";
import "react-toastify/dist/ReactToastify.css";
import "./App.css";

import AddLesson from "./components/lesson_management/addLesson";
import Lessons from "./components/lesson_management/lessons";
import EditLesson from "./components/lesson_management/editLesson";
import Classes from "./components/class_management/classes";
import addWeeklySchedule from "./components/weekly_schedule/addWeeklySchedule";
import AddClass from "./components/class_management/addClass";
import EditClass from "./components/class_management/editClass";
import ViewWeeklySchedule from "./components/weekly_schedule/viewWeeklySchedule";

class App extends Component {
  //state = { userId: "", userProfile: { data: [] } }; //comment it
  async componentDidMount() {
    const userId = auth.getCurrentUser();
    const userProfile = await auth.getUserProfile(userId);
    this.setState({ userId, userProfile });
  }
  render() {
    let LoggedIn = null;
    if (this.state) {
      const { userId, userProfile } = this.state;
      LoggedIn = userId && userProfile;
    }
    //LoggedIn = true; //comment it
    return (
      <React.Fragment>
        <ToastContainer />
        {!this.state && <div id="loader"></div>}
        {this.state && LoggedIn && (
          <React.Fragment>
            <NavBar userProfile={this.state.userProfile} />
            <div className="container-fluid">
              <div className="row">
                <SideBar userProfile={this.state.userProfile} />
                <div id="main" className="col p-5">
                  <Switch>
                    <Route path="/logout" exact component={LogOut} />
                    <Route path="/register" exact component={RegisterForm} />
                    <Route path="/users" exact component={Users} />
                    <Route
                      path="/edit-user/changepass/:id"
                      component={ChangePassByModerator}
                    />
                    <Route path="/edit-user/:id" component={EditUser} />
                    <Route
                      path="/changePass"
                      component={ChangePassFromProfile}
                    />

                    <Route path="/forgot-password" component={ForgotPass} />

                    <Route path="/add-news" exact component={AddNews} />
                    <Route path="/news/:id" exact component={ViewNews} />
                    <Route path="/news" exact component={News} />
                    <Route path="/edit-news/:id" component={EditNews} />

                    <Route path="/add-tutorial" exact component={AddTutorial} />
                    <Route path="/edit-tutorial/:id" component={EditTutorial} />
                    <Route
                      path="/tutorials/:id"
                      exact
                      component={ViewTutorial}
                    />
                    <Route path="/tutorials" exact component={Tutorials} />

                    <Route path="/add-lesson" exact component={AddLesson} />
                    <Route
                      path="/edit-lesson/:id"
                      exact
                      component={EditLesson}
                    />
                    <Route path="/lessons" exact component={Lessons} />

                    <Route path="/add-class" exact component={AddClass} />
                    <Route path="/edit-class/:id" exact component={EditClass} />
                    <Route path="/classes" exact component={Classes} />

                    <Route
                      path="/add-weekly-schedule/:data"
                      exact
                      component={addWeeklySchedule}
                    />
                    <Route
                      path="/weekly-schedule/:data"
                      exact
                      component={ViewWeeklySchedule}
                    />

                    <Route path="/not-found" component={NotFound} />
                    <Route path="/" exact component={Home} />

                    <Redirect from="/login" to="/profile" />
                    <Redirect to="/not-found" />
                  </Switch>
                </div>
              </div>
            </div>
          </React.Fragment>
        )}
        {this.state && !LoggedIn && (
          <Switch>
            <Route path="/forgot-password" component={ForgotPass} />
            <Route path="/login" component={LoginForm} />
            <Redirect to="/login" />
          </Switch>
        )}
      </React.Fragment>
    );
  }
}

export default App;
