import React from "react";
import { Redirect } from "react-router-dom";
import ReactQuill from "react-quill";
import { toast } from "react-toastify";
import Joi from "joi-browser";
import axios from "axios";
import auth from "../../services/authService";
import Form from "../common/form";
import "react-quill/dist/quill.snow.css";
import logo from "../../images/no-img.jpg";
class EditNews extends Form {
  state = {
    validNews: "",
    uploadData: { uploadPercentage: "", file: "", cancelToken: "" },
    data: {
      title: "",
      img: "",
      content: "",
      author_id: "",
      release_date: "",
      id: "",
    },
    errors: {},
  };
  schema = {
    title: Joi.string() //change to number
      .required()
      .label("تیتر")
      .error((errors) => this.ErrorsLang(errors)),
    content: Joi.any(),
    img: Joi.any(),
    author_id: Joi.any(),
    release_date: Joi.any(),
    id: Joi.any(),
  };
  async componentDidMount() {
    if (this.props.match.params.id === "0") return;
    try {
      const result = await auth.getNews(this.props.match.params.id);
      const resData = result.data.data;
      const validNews = true;
      let data = { ...this.state.data };
      data.title = resData.title;
      data.img = resData.pic;
      data.content = resData.content;
      data.author_id = resData.author_id;
      data.release_date = resData.release_date;
      data.id = resData.id;
      this.setState({ validNews, data });
    } catch (e) {
      const validNews = false;
      this.setState({ validNews });
      toast.error(e.response.data.error[0]);
    }
  }
  uploadImg = (e) => {
    const { uploadData } = { ...this.state };
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    uploadData.file = e.target.files[0]; // should be in the IF ?
    if (e.target.files[0]) uploadData.file.cancelToken = source;
    this.setState({ uploadData });
    let data = new FormData();
    this.setState({ uploadData });
    const user_id = auth.getCurrentUser();
    data.append("user_id", user_id);
    data.append("image", e.target.files[0], e.target.files[0].name); //EDIT
    const options = {
      cancelToken: source.token,
      onUploadProgress: (progressEvent) => {
        const { loaded, total } = progressEvent;
        let percent = Math.floor((loaded * 100) / total);
        console.log(`${loaded}kb of ${total}kb | ${percent}%`);
        if (percent < 100) {
          const uploadData = { ...this.state.uploadData };
          uploadData.uploadPercentage = percent;
          this.setState({ uploadData });
        }
      },
    };
    axios
      .post(
        "https://myrest-novinschool.fandogh.cloud/Users/App/images/api/",
        data,
        options
      )
      .then((res) => {
        //console.log("error", res);
        const uploadData = { ...this.state.uploadData };
        const data = { ...this.state.data };

        data.img = "/media/" + res.data.data;
        uploadData.uploadPercentage = 100;
        toast.success("تصویر آپلود شد");
        this.setState({ data, uploadData }, () => {
          setTimeout(() => {
            const uploadData = { ...this.state.uploadData };
            uploadData.uploadPercentage = 0;
            this.setState({ uploadData });
          }, 1000);
        });
      })
      .catch((thrown) => {
        toast.error("تصویر آپلود نشد.");
        // console.log(thrown);
        const uploadData = { ...this.state.uploadData };
        uploadData.uploadPercentage = 0;
        this.setState({ uploadData });
      });
  };
  cancelImgUpload = () => {
    const uploadData = { ...this.state.uploadData };
    uploadData.file.cancelToken.cancel();
    const CancelToken = axios.CancelToken;
    uploadData.cancelToken = CancelToken.source();
    uploadData.uploadPercentage = "";
    uploadData.file = "";
    this.setState({ uploadData });
    document.getElementById("img").value = ""; // for real? :D
  };
  doSubmit = async () => {
    try {
      const { data } = this.state;
      await auth.updateNews(data);
      //toast.success("خبر با موفقیت ثبت شد.");
      window.location = process.env.PUBLIC_URL + "/news/" + data.id;
    } catch (ex) {
      toast.error("تغییرات اعمال نشد :(");
    }
  };
  render() {
    if (this.props.match.params.id === "0") return <Redirect to="/news" />;
    const { img } = this.state.data;
    const { validNews } = this.state;
    return (
      <React.Fragment>
        {validNews === "" && <div id="loader"></div>}
        {validNews === false && <Redirect to="/news" />}
        {validNews === true && (
          <div className="card col-12">
            <div className="card-body">
              <div className="d-flex align-items-center justify-content-center mb-3">
                <div className="text-center">
                  <img
                    src={img ? auth.apiUrl + img : logo}
                    alt="img"
                    className="rounded form-image"
                    height="180px"
                  />
                  <h6 className="text-center pt-3 pb-1 border-bottom border-1 ">
                    تغییر خبر
                  </h6>
                </div>
              </div>
              <div className="form-group col-md-5 px-0">
                <label>تصویر</label>
                <div className="custom-file">
                  <label
                    className="custom-file-label"
                    htmlFor="img"
                    data-browse="جستجو"
                  >
                    <small>
                      {this.state.uploadData.file !== ""
                        ? this.state.uploadData.file.name
                        : "آپلود تصویر"}
                    </small>
                  </label>
                  <input
                    type="file"
                    onChange={this.uploadImg}
                    className="custom-file-input"
                    id="img"
                    name="img"
                  />
                </div>
                <div className="form-group">
                  {this.state.uploadData.uploadPercentage > 0 && (
                    <React.Fragment>
                      <span
                        className="mr-n4 text-danger text-weight-bold"
                        style={{ position: "absolute" }}
                        onClick={this.cancelImgUpload}
                      >
                        <i className="far fa-times-circle"></i>
                      </span>
                      <div className="progress" style={{ height: "20px" }}>
                        <div
                          className="progress-bar progress-bar-striped progress-bar-animated"
                          role="progressbar"
                          aria-valuenow={this.state.uploadData.uploadPercentage}
                          aria-valuemin="0"
                          aria-valuemax="100"
                          style={{
                            width: `${this.state.uploadData.uploadPercentage}%`,
                          }}
                        >
                          {`${this.state.uploadData.uploadPercentage}%`}
                        </div>
                      </div>
                    </React.Fragment>
                  )}
                </div>
              </div>
              <div className="mb-3">
                <form onSubmit={this.handleSubmit}>
                  {this.renderInput("title", "تیتر خبر")}
                  <ReactQuill
                    theme="snow"
                    value={this.state.data.content}
                    onChange={(e) => this.quilHandleChange(e)}
                    modules={{
                      toolbar: [
                        ["strike", "underline", "italic", "bold"],
                        [{ color: [] }, { background: [] }],
                        [{ script: "super" }, { script: "sub" }],
                        [{ indent: "-1" }, { indent: "+1" }, { align: [] }],
                        [{ list: "ordered" }, { list: "bullet" }],
                        [{ size: ["small", false, "large", "huge"] }],
                        [{ header: [1, 2, 3, 4, 5, 6, false] }],
                      ],
                    }}
                  />
                  <div className="text-center pt-4">
                    {this.renderButton("اعمال تغییرات", "mt-3")}
                  </div>
                </form>
              </div>
            </div>
          </div>
        )}
      </React.Fragment>
    );
  }
}

export default EditNews;
