import React, { Component } from "react";
import { toast } from "react-toastify";
import _ from "lodash";
import $ from "jquery";
import Modal from "../common/modal";
import Lbs from "./../common/lbs";
import auth from "../../services/authService";
import { paginate } from "../../js/paginate";
import ClassesTable from "./classesTable";
import Pagination from "../common/pagination";
import Select from "../common/select";

class Classes extends Component {
  state = {
    classes: [], //id,name,section_id
    section: [
      { name: "همه", _id: "" },
      { name: "پیش دبستانی", _id: "0" },
      { name: "اول", _id: "1" },
      { name: "دوم", _id: "2" },
      { name: "سوم", _id: "3" },
      { name: "چهارم", _id: "4" },
      { name: "پنجم", _id: "5" },
      { name: "ششم", _id: "6" },
      { name: "هفتم", _id: "7" },
      { name: "هشتم", _id: "8" },
      { name: "نهم", _id: "9" },
      { name: "پرسنل", _id: "10" },
    ],
    modalData: "",
    IsRequestDone: false,
    currentPage: 1,
    pageSize: 4,
    sortColumn: { path: "name", order: "asc" },
  };

  async componentDidMount() {
    try {
      const allClasses = await auth.getAllClasses();
      const classes = this.convertSectionToString(allClasses.data.data);
      this.setState({ classes, IsRequestDone: true });
    } catch (error) {
      toast.error("مشکلی در ارتباط با سرور پیش آمد :(");
      this.setState({ IsRequestDone: true });
    }
  }

  handleDelete = async (classId) => {
    const originalClasses = this.state.classes;
    const classes = this.state.classes.filter((n) => n.id !== classId);
    this.setState({ classes });
    try {
      await auth.deleteClass(classId);
      toast.success("کلاس با موفقیت حذف شد.");
    } catch (e) {
      toast.error("حذف کلاس با شکست مواجه شد.");
      this.setState({ classes: originalClasses });
    }
  };
  handleModal = (modalData) => {
    this.setState({ modalData });
    $("#delete_class").modal("show");
  };
  handleEdit = (classRoom) => {
    window.location = process.env.PUBLIC_URL + "/edit-class/" + classRoom.id;
  };
  handlescheduleView = (classRoom) => {
    window.location =
      process.env.PUBLIC_URL +
      `/weekly-schedule/${classRoom.section._id}&${classRoom.id}&${classRoom.name}`;
  };
  handleScheduleAdd = (classRoom) => {
    window.location =
      process.env.PUBLIC_URL +
      `/add-weekly-schedule/${classRoom.section._id}&${classRoom.id}&${classRoom.name}`;
  };
  handlePageChange = (page) => {
    this.setState({ currentPage: page });
  };

  handleSectionSelect = ({ currentTarget: input }) => {
    this.setState({ selectedSection: input.value, currentPage: 1 });
  };
  handleSort = (sortColumn) => {
    this.setState({ sortColumn });
  };

  getPagedData = () => {
    const {
      pageSize,
      currentPage,
      sortColumn,
      selectedSection,
      classes: allClasses,
    } = this.state;
    const filtered = selectedSection
      ? allClasses.filter((l) => {
          console.log(selectedSection);
          return l.section._id === selectedSection;
        })
      : allClasses;

    const sorted = _.orderBy(filtered, [sortColumn.path], [sortColumn.order]);

    const classes = paginate(sorted, currentPage, pageSize);

    return { totalCount: filtered.length, data: classes };
  };
  convertSectionToString = (classes) => {
    for (let i = 0; i < classes.length; i++) {
      switch (classes[i].section_id) {
        case "0":
          classes[i].section = { name: "پیش دبستانی", _id: "0" };
          delete classes[i].section_id;
          break;
        case "1":
          classes[i].section = { name: "اول", _id: "1" };
          delete classes[i].section_id;
          break;
        case "2":
          classes[i].section = { name: "دوم", _id: "2" };
          delete classes[i].section_id;
          break;
        case "3":
          classes[i].section = { name: "سوم", _id: "3" };
          delete classes[i].section_id;
          break;
        case "4":
          classes[i].section = { name: "چهارم", _id: "4" };
          delete classes[i].section_id;
          break;
        case "5":
          classes[i].section = { name: "پنجم", _id: "5" };
          delete classes[i].section_id;
          break;
        case "6":
          classes[i].section = { name: "ششم", _id: "6" };
          delete classes[i].section_id;
          break;
        case "7":
          classes[i].section = { name: "هفتم", _id: "7" };
          delete classes[i].section_id;
          break;
        case "8":
          classes[i].section = { name: "هشتم", _id: "8" };
          delete classes[i].section_id;
          break;
        case "9":
          classes[i].section = { name: "نهم", _id: "9" };
          delete classes[i].section_id;
          break;
        case "10":
          classes[i].section = { name: "پرسنل", _id: "10" };
          delete classes[i].section_id;
          break;
        default:
          break;
      }
    }
    return classes;
  };

  render() {
    const { length: count } = this.state.classes;
    const { pageSize, currentPage, sortColumn, IsRequestDone } = this.state;
    if (!IsRequestDone)
      return (
        <div className="card col-12">
          <div className="card-body">
            <Lbs />
          </div>
        </div>
      );
    if (count === 0)
      return (
        <div className="card col-12">
          <div className="card-body">
            <p>چیزی پیدا نشد :(</p>
          </div>
        </div>
      );

    const { totalCount, data: classes } = this.getPagedData();

    return (
      <React.Fragment>
        <Modal
          modalId="delete_class"
          dataId={this.state.modalData}
          handleDelete={this.handleDelete}
        />
        <div className="card col-12">
          <div className="card-body">
            <div className="form-row">
              {
                <Select
                  name="section"
                  label="مقطع"
                  customClass="col-md-3"
                  options={this.state.section}
                  onChange={this.handleSectionSelect}
                />
              }
            </div>
            <div className="col">
              <p className="small text-muted">نتیجه: {totalCount} کلاس</p>
              <ClassesTable
                classes={classes}
                sortColumn={sortColumn}
                onClick={this.handleModal}
                onEdit={this.handleEdit}
                onSort={this.handleSort}
                onScheduleAdd={this.handleScheduleAdd}
                onscheduleView={this.handlescheduleView}
              />
              <Pagination
                itemsCount={totalCount}
                pageSize={pageSize}
                currentPage={currentPage}
                onPageChange={this.handlePageChange}
              />
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Classes;
