import React, { Component } from "react";
import { toast } from "react-toastify";
import _ from "lodash";
import $ from "jquery";
import Modal from "../common/modal";
import Lbs from "./../common/lbs";
import auth from "../../services/authService";
import { paginate } from "../../js/paginate";
import LessonsTable from "./lessonsTable";
import Pagination from "../common/pagination";
import Select from "../common/select";

class Lessons extends Component {
  state = {
    lessons: [],
    section: [
      { name: "همه", _id: "" },
      { name: "پیش دبستانی", _id: "0" },
      { name: "اول", _id: "1" },
      { name: "دوم", _id: "2" },
      { name: "سوم", _id: "3" },
      { name: "چهارم", _id: "4" },
      { name: "پنجم", _id: "5" },
      { name: "ششم", _id: "6" },
      { name: "هفتم", _id: "7" },
      { name: "هشتم", _id: "8" },
      { name: "نهم", _id: "9" },
      { name: "پرسنل", _id: "10" },
    ],
    modalData: "",
    IsRequestDone: false,
    currentPage: 1,
    pageSize: 4,
    sortColumn: { path: "title", order: "asc" },
  };

  async componentDidMount() {
    try {
      const allLessons = await auth.getAllLessons();
      const lessons = this.convertSectionToString(allLessons.data.data);
      this.setState({ lessons, IsRequestDone: true });
    } catch (error) {
      toast.error("مشکلی در ارتباط با سرور پیش آمد :(");
      this.setState({ IsRequestDone: true });
    }
  }

  handleDelete = async (lessonId) => {
    const originalLessons = this.state.lessons;
    const lessons = this.state.lessons.filter((n) => n.id !== lessonId);
    this.setState({ lessons });
    try {
      await auth.deleteLesson(lessonId);
      toast.success("درس با موفقیت حذف شد.");
    } catch (e) {
      toast.error("حذف درس با شکست مواجه شد.");
      this.setState({ lessons: originalLessons });
    }
  };
  handleModal = (modalData) => {
    this.setState({ modalData });
    $("#delete_lesson").modal("show");
  };
  handleEdit = (lesson) => {
    window.location = process.env.PUBLIC_URL + "/edit-lesson/" + lesson.id;
  };
  handlePageChange = (page) => {
    this.setState({ currentPage: page });
  };

  handleSectionSelect = ({ currentTarget: input }) => {
    this.setState({ selectedSection: input.value, currentPage: 1 });
  };
  handleSort = (sortColumn) => {
    this.setState({ sortColumn });
  };

  getPagedData = () => {
    const {
      pageSize,
      currentPage,
      sortColumn,
      selectedSection,
      lessons: allLessons,
    } = this.state;
    const filtered = selectedSection
      ? allLessons.filter((l) => {
          console.log(selectedSection);
          return l.section._id === selectedSection;
        })
      : allLessons;

    const sorted = _.orderBy(filtered, [sortColumn.path], [sortColumn.order]);

    const lessons = paginate(sorted, currentPage, pageSize);

    return { totalCount: filtered.length, data: lessons };
  };
  convertSectionToString = (lessons) => {
    for (let i = 0; i < lessons.length; i++) {
      switch (lessons[i].section_id) {
        case "0":
          lessons[i].section = { name: "پیش دبستانی", _id: "0" };
          delete lessons[i].section_id;
          break;
        case "1":
          lessons[i].section = { name: "اول", _id: "1" };
          delete lessons[i].section_id;
          break;
        case "2":
          lessons[i].section = { name: "دوم", _id: "2" };
          delete lessons[i].section_id;
          break;
        case "3":
          lessons[i].section = { name: "سوم", _id: "3" };
          delete lessons[i].section_id;
          break;
        case "4":
          lessons[i].section = { name: "چهارم", _id: "4" };
          delete lessons[i].section_id;
          break;
        case "5":
          lessons[i].section = { name: "پنجم", _id: "5" };
          delete lessons[i].section_id;
          break;
        case "6":
          lessons[i].section = { name: "ششم", _id: "6" };
          delete lessons[i].section_id;
          break;
        case "7":
          lessons[i].section = { name: "هفتم", _id: "7" };
          delete lessons[i].section_id;
          break;
        case "8":
          lessons[i].section = { name: "هشتم", _id: "8" };
          delete lessons[i].section_id;
          break;
        case "9":
          lessons[i].section = { name: "نهم", _id: "9" };
          delete lessons[i].section_id;
          break;
        case "10":
          lessons[i].section = { name: "پرسنل", _id: "10" };
          delete lessons[i].section_id;
          break;
        default:
          break;
      }
    }
    return lessons;
  };

  render() {
    const { length: count } = this.state.lessons;
    const { pageSize, currentPage, sortColumn, IsRequestDone } = this.state;
    if (!IsRequestDone)
      return (
        <div className="card col-12">
          <div className="card-body">
            <Lbs />
          </div>
        </div>
      );
    if (count === 0)
      return (
        <div className="card col-12">
          <div className="card-body">
            <p>چیزی پیدا نشد :(</p>
          </div>
        </div>
      );

    const { totalCount, data: lessons } = this.getPagedData();

    return (
      <React.Fragment>
        <Modal
          modalId="delete_lesson"
          dataId={this.state.modalData}
          handleDelete={this.handleDelete}
        />
        <div className="card col-12">
          <div className="card-body">
            <div className="form-row">
              {
                <Select
                  name="section"
                  label="مقطع"
                  customClass="col-md-3"
                  options={this.state.section}
                  onChange={this.handleSectionSelect}
                />
              }
            </div>
            <div className="col">
              <p className="small text-muted">نتیجه: {totalCount} درس</p>
              <LessonsTable
                lessons={lessons}
                sortColumn={sortColumn}
                onClick={this.handleModal}
                onEdit={this.handleEdit}
                onSort={this.handleSort}
              />
              <Pagination
                itemsCount={totalCount}
                pageSize={pageSize}
                currentPage={currentPage}
                onPageChange={this.handlePageChange}
              />
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Lessons;
